import { Button, Form, Input, Row, Typography } from "antd";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
// import { Link, useNavigate } from "react-router-dom";
import Cookies from "universal-cookie";
import { sendOTP, validateOTP } from "../api/auth";
import { useState } from "react";

const LogIn = () => {
  const [isOTPSent, setIsOTPSent] = useState(false);
  // const navigate = useNavigate();

  const onFinish = async (values: any) => {
    if (!isOTPSent) {
      const sentSuccessfully = await sendOTP(values.email);
      sentSuccessfully && setIsOTPSent(true);
    } else {
      const token = await validateOTP(values.email, values.opt);

      if (token) {
        const cookies = new Cookies();
        cookies.set("sessionToken", `${values.email} ${token}`, { path: "/" });
      }
    }

    // location.replace("/flow");
  };

  return (
    <>
      <Row justify="center" style={{ marginTop: 160 }}>
        <Typography.Title>Cyber Club</Typography.Title>
      </Row>

      <Row justify="center" style={{ margin: 20 }}>
        <Form onFinish={onFinish}>
          <Form.Item
            name="email"
            rules={[{ required: true, message: "Please enter your email" }]}
          >
            <Input prefix={<UserOutlined />} placeholder="Email" />
          </Form.Item>
          {isOTPSent && (
            <Form.Item
              name="otp"
              rules={[
                {
                  required: true,
                  message: "Please input your verification code",
                },
              ]}
            >
              <Input.Password prefix={<LockOutlined />} placeholder="xxxxxx" />
            </Form.Item>
          )}

          <Form.Item>
            <Button type="primary" htmlType="submit" block>
              {isOTPSent ? "Log In" : "Send code"}
            </Button>
          </Form.Item>
        </Form>
      </Row>
    </>
  );
};

export default LogIn;
